import React from 'react';
import "../App.css";

const Modal = ( props ) => {
    // , ,     
    const { open, close } = props;

    return (
        //   openModal  .
        <div className={ open ? 'openModal modal' : 'modal' }>
            { open ? (  
                <section>
                 <footer>
                        <button className="close" onClick={close}> close </button>
                    </footer>
                    <main>
                        {props.children}
                    </main>
                </section>
            ) : null }
        </div>
    )
}

export default Modal;