import React,{useState} from 'react';
import styled from 'styled-components'
import Header from '../components/Header'
import axios from 'axios'

const Withdrawal = styled.div`
    width:30rem;
    height:20rem;
    border:1.5px solid #e2e2e2;
    margin:0 auto;
    margin-top:5rem;
    border-radius:1rem;
    position:relative;
`
const Title = styled.div`
    margin-top:1rem;
    margin-left:1rem;
    font-weight:600;
    font-size:1.5rem;
`
const Input = styled.input`
    margin-top:2rem;
    width:25rem;
    height:2rem;
    border-radius:1rem;
    border:1.5px solid #e2e2e2;
    font-size:1rem;
    background:#f9f9f9;
    display:block;
    margin:0 auto;
    margin-top:2rem;
    &:focus{
        outline:none;
    }
`
const Warn = styled.div`
    padding-left:2rem;
    margin-top:1rem;
    font-size:0.85rem;
    color:gray;
    line-height:1.4rem;
`
const Submit = styled.button`
    width:25rem;
    display:block;
    margin:0 auto;
    margin-top:1rem;
    height:2.5rem;
    position:absolute;
    bottom:1rem;
    right:2.5rem;
    border-radius:1rem;
    border:none;
    background:gray;
    color:white;
    font-size:1.3rem;
`
const SiteInfo = styled.div`
   text-align:center; 
   margin-top:0.8rem;
   font-size:0.75rem;
   color:gray;
`

function App(){
    let token = localStorage.getItem('token')
    let [pw,setPw] = useState('')
    
    const handleWithdrawal =()=>{
        axios({
    method: "post",
    url: "/api/users/withdrawal",
    headers: { "authorization": "Bearer "+token },
    data:{
        password:pw
    }
    })
    .then(function (res) {
        if(res.data==='ok'){
            alert(' .')
            window.location.href='/login'
            localStorage.removeItem('token')
        }else if(res.data==='incorrect'){
            alert('  .')
        }
    })
  .catch(function (response) {
  });
    }
    
    const handlePassword =(e)=>{
        setPw(e.target.value)
    }
    
    return(
        <>
        <Header/>
        <Withdrawal>
        <Title></Title>
        <Input type='password' placeholder='' onChange={handlePassword}/>
        <Warn>  ,     .<br/>    , ()  .<br/>    .</Warn>
        <Submit onClick={handleWithdrawal}></Submit>
        </Withdrawal>
        <SiteInfo><span onClick={()=>{window.location.href='/page/ServiceAgreement'}}></span>&nbsp; &nbsp;<span onClick={()=>{window.location.href='/page/privacy'}}></span>&nbsp; &nbsp;&nbsp; &nbsp;&copy;<span onClick={()=>{window.location.href='/'}}></span></SiteInfo>
        </>
        )
}

export default App;