import React from 'react';
import "../../App.css";

const Modal = ( props ) => {
    // , ,     
    const { open, close, header,footer } = props;

    return (
        //   openModal  .
        <div className={ open ? 'openModal singomodal' : 'singomodal' }>
            { open ? (  
                <section>
                    <header>
                        {header}
                        <button className="close" onClick={close}> &times; </button>
                    </header>
                    <main>
                        {props.children}
                    </main>
                    <footer>
                    {footer}
                       
                    </footer>
                </section>
            ) : null }
        </div>
    )
}

export default Modal;