import React,{useEffect, useState} from 'react';
import styled from 'styled-components';
import Modal from '../Modal';
import axios from 'axios';

const CardSubmit = styled.button`
    width:20rem;
    height:1.8rem;
    margin: 0 auto;
    background:#6c757d;
    display:block;
    border-radius:1rem;
    font-size:1.2rem;
    color:white;
    margin-bottom:0.5rem;
     @media all and (max-width:479px) {
     width:100%;
 }
`
const CardNoti = styled.div`
    width:20rem;
    margin:0 auto;
    margin-bottom:0.5rem;
    color:gray;
    font-size:0.8rem;
     @media all and (max-width:479px) {
     width:100%;
 }
`
const Input = styled.input`
    margin:0 auto;
    display:block;
    width:20rem;
    height:1.8rem;
    border-radius:0.5rem;
    background:#f9f9f9;
    border:none;
    margin-bottom:0.1rem;
    border:0.1rem solid #c6c6c6;
    font-size:1rem;
    &:focus{
        outline:none;
    }
     @media all and (max-width:479px) {
     width:90%;
 }
`

function App(props) {
    const [ modalOpen, setModalOpen ] = useState(false);
    const [cardNoti, setCardNoti] = useState('')
    const [Nick, setNick] = useState('')
    let token = localStorage.getItem('token')
    
    useEffect(() => {
    setModalOpen(props.open)
  }, [props.open]);
    
      
  
  const closeModal = (type) => {
        props.setModalOpen(false);
    }
  
  const handleChangeNick = () => {
      if(Nick.length>5||Nick.length<2||Nick===''||Nick===''||Nick===''){
          setCardNoti('   .')
          return false;
          }
      
  axios({
    method: "post",
    url: "/api/users/changeNick",
    data: {nickname:Nick},
    headers:{"authorization": "Bearer "+token}
    })
    .then(function (res) {
        if(res.data==='change'){
            setNick('')
            alert('.')
            props.setModalOpen(false);
        }else if(res.data==='already change'){
            setCardNoti('  30  .')
        }
    })
  .catch(function (res) {
      alert(res)
  });
  }
  const handleNick =(e)=>{
      let nick=e.target.value
      if(nick.length>5){
          setCardNoti(' 5  .')
      }else if(nick.length<2){
          setCardNoti(' 2  .')
      }else if(nick===''||nick===''||nick===''){
          setCardNoti('   .')
      }else{
          setCardNoti('')
      }
          setNick(e.target.value)
          
          
  }
    return(
            <Modal open={ modalOpen } close={ closeModal }>
                <Input type='text' placeholder=' ' onChange={handleNick}
             />
               <CardNoti style={{color:'#c62918'}}>{cardNoti}</CardNoti>
                <div style={{'margin-bottom':'2rem'}}/>
                <CardSubmit onClick={handleChangeNick}> </CardSubmit>
            <CardNoti>  30     .</CardNoti>                
            </Modal>
        )
}

export default App;